// src/App.tsx
import React from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import theme from './styles/theme'; // Make sure the path is correct
import LandingPage from './pages/LandingPage';
import ReflectPage111 from './pages/ReflectPage111';
import GrowPage from './pages/GrowPage';
import GrowModuleDetailPage from './pages/GrowModuleDetailPage';
import HistoryPage from './pages/HistoryPage';
import AnalysisDetailPage from './pages/AnalysisDetailPage';
import { AuthProvider } from './contexts/AuthContext';
import './styles/global.css';

function App() {
  return (
    <ThemeProvider theme={theme}>
      <AuthProvider>
        <Router>
          <Routes>
            <Route path="/" element={<LandingPage />} />
            <Route path="/reflect" element={<ReflectPage111 />} />
            <Route path="/reflect111" element={<ReflectPage111 />} />
            <Route path="/grow" element={<GrowPage />} />
            <Route path="/grow/:moduleId" element={<GrowModuleDetailPage />} />
            <Route path="/history" element={<HistoryPage />} />
            <Route path="/analysis/:id" element={<AnalysisDetailPage />} />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </Router>
      </AuthProvider>
    </ThemeProvider>
  );
}

export default App;